import React from 'react'
import { graphql, Link } from 'gatsby'
import { DiscussionEmbed } from "disqus-react";
// import Helmet from 'react-helmet'
import Layout from '../components/layout'
import { rhythm, scale } from "../utils/typography"
import get from 'lodash/get'
import SEO from "../components/seo"
import markdown from "../css/markdown.module.css"
// import "../css/images/style.css"
import Form from "../components/form"
// import "../css/main.css"
import rehypeReact from "rehype-react"
import CustomImage from "../components/CustomImage"

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: { "custom-image": CustomImage },
}).Compiler

class Markdown extends React.Component {
  render() {
    const post = get(this.props, 'data.markdownRemark')
    // const { previous, next } = this.props.pageContext
    const { previous, next } = get(this.props, 'pageContext')
    const siteTitle = get(this.props, 'data.site.siteMetadata.title')
    const disqusShortname = "muhzulzidan";
    const disqusConfig = {
      identifier: post.id,
      title: post.frontmatter.title,};
    return (
      // <Layout location={this.props.location}>
      //   <div >
      //     <Helmet title={`${post.frontmatter.title} | ${siteTitle}`} />
      //     <div >
      //       {/* <Img
             
      //         alt={post.title.value}
      //         fluid={post.image.value.childImageSharp.fluid}
      //       /> */}
      //     </div>
      //     <div >
      //       <h1 >{post.frontmatter.title}</h1>
      //       <p
      //         style={{
      //           display: 'block',
      //         }}
      //       >
      //         {post.frontmatter.date}
      //       </p>
      //       <div
      //         dangerouslySetInnerHTML={{
      //           __html: post.html,
      //         }}
      //       />

      //       {/* <div>{ renderAst(post.htmlAst) }</div> */}
      //     </div>
      //   </div>
      // </Layout>
      <Layout location={this.props.location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={ post.excerpt}
      />
      <article>
        <header>
        
          <h1
            style={{
              marginTop: rhythm(1),
              marginBottom: 0,
              fontWeight:rhythm(1),
              fontSize:rhythm(.9),
              ...scale(.8),
            }}
          >
            {post.frontmatter.title}
            
          </h1>

            <span
              style={{
                ...scale(-1 / 5),
                display: `block`,
                // marginBottom: rhythm(1),
                // float: `right`,
              }}
            >
               {/* <br/> */}
              written by {post.frontmatter.author} on {post.frontmatter.date}
            </span>

          {/* <br/> */}
        </header>
        {/* <section 
        className={markdown.section}
        dangerouslySetInnerHTML={{ __html: post.html }}  /> */}
        <section 
        className={markdown.section}>
        {renderAst(post.htmlAst)}</section>
        <hr
          
        />
        <Form/>
        <hr
          style={{
            marginTop:rhythm(.8),
            marginBottom: rhythm(2),
          }}
        />
        <aside>
        
        <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
        </aside>

       
        {/* <footer>
          <Bio />
         
        </footer> */}
      </article>

      <nav>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.frontmatter.slug} rel="prev">
                ← {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.frontmatter.slug} rel="next">
                {next.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
    )
  }
}

export default Markdown

export const pageQuery = graphql`
query Markdown ($slug: String!){
  site {
    siteMetadata {
      title
    }
  }
  markdownRemark (frontmatter: {slug: {eq: $slug}}){
    frontmatter {
      title
      date(formatString: "MMMM DD, YYYY")
      slug
      tags
      author
    }
    htmlAst
    html
    id
    excerpt(pruneLength: 160)
  }
}

`